import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ServiceService } from 'src/app/core/service.service';
@Component({
  selector: 'app-add-ward',
  templateUrl: './add-ward.component.html',
  styleUrls: ['./add-ward.component.scss']
})
export class AddWardComponent implements OnInit {
  public dialogRef: MatDialogRef<AddWardComponent>
  @Inject(MAT_DIALOG_DATA) public data:any
  subscriptions = new Subscription();

  constructor(private fb: FormBuilder, private router: Router , private service : ServiceService , private route : ActivatedRoute) {}

  wardADD: FormGroup;
  submitted = false
  id
  ProductType
  allData
  selectProduct


  async ngOnInit() {
 console.log(this.data);
 
    this.wardADD = this.fb.group({
      wardName: ['', [Validators.required]],
      roomStart : ['', [Validators.required]],
      roomEnd: ['', [Validators.required]],
      price: ['', [Validators.required]],
      // status: ['', [Validators.required]],
    });
  }

  get f() { return this.wardADD.controls; }

  next(){
    console.log(this.wardADD)
    this.submitted = true

      if (this.wardADD.invalid) {
        return;
      }
    this.service.spinnerStart()
    this.service.addWard(this.wardADD.value).subscribe(res=>{
      console.log(res);
      this.service.spinnerClose()
      this.dialogRef.close();
    },
    error=>{
      console.log(error);
      this.service.spinnerClose()

    })
  }




  





  




  






 

  ngOnDestroy() {
    if(this.subscriptions){
      this.subscriptions.unsubscribe();
    }
  }
 
}


