import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard.component';
import { WardListComponent } from './ward-list/ward-list.component';
import { OpdRegistrationComponent } from './opd-registration/opd-registration.component';

const routes: Routes = [
  { path: '', component: DashboardComponent },
  { path: 'ward', component: WardListComponent },
  { path: 'registration', component: OpdRegistrationComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
