import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ServiceService } from 'src/app/core/service.service';

@Component({
  selector: 'app-opd-registration',
  templateUrl: './opd-registration.component.html',
  styleUrls: ['./opd-registration.component.scss']
})
export class OpdRegistrationComponent implements OnInit {

  constructor(private fb: FormBuilder, private router: Router, private service: ServiceService,
    private route: ActivatedRoute) { }
    registration: FormGroup;
  submitted = false
  id
  allData
  async ngOnInit() {
    this.registration = this.fb.group({
      patientname: ['', Validators.required],
      age: ['', [Validators.required, Validators.pattern('[0-9]{10}$')]],
      mobilenumber: ['', Validators.required],
      address:['', Validators.required],
      email: ['', [Validators.required, Validators.email, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,3}$')]],
      referenceby: ['', Validators.required],
      referencemobile: ['', Validators.required],
      
      gender: ['0', Validators.required],
      department: ['0', Validators.required],
      panel: ['0', Validators.required],
      doctor: ['0', Validators.required],
      adharno: ['', Validators.required],
      calculate_amount: this.fb.array([
        this.addnewBlock()
      ])
     
      // ClientSPOCMobile: ['', [Validators.required, Validators.minLength(10), Validators.pattern('[0-9]{10}$')]],
    });

    


  }

  addnewBlock() {
    return this.fb.group({
      date: ['', [Validators.required]],
      categorytype: ['', [Validators.required]],
      qty: ['', [Validators.required]],
      amount: ['', [Validators.required]],
      discount_percentage: ['', [Validators.required]],
      discount_amount: ['', [Validators.required]],
      tax_percentage: ['', [Validators.required]],
      tax_amount: ['', [Validators.required]],
      netamount: ['', [Validators.required]],
      remark: ['', [Validators.required]],
    });
  }


  addBlock() {
    this.submitted = false
    const control = <FormArray>this.registration.controls['calculate_amount'];
    control.push(this.addnewBlock());
  }

  removeBlock(index) {
    if (index == 0) {
      return
    }
    const control = <FormArray>this.registration.controls['calculate_amount'];
    control.removeAt(index)
  }  
  get f() { return this.registration.controls; }


  async next() {
    this.submitted = true
    if (this.registration.invalid || this.registration.value.DemoAccount == '0') {
      return;
    }
    
  }

}
